
"use client";
import { useEffect, useState } from 'react';

export default function DealsTable() {
  const [deals, setDeals] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch('/api/deals')
      .then(async res => {
        try {
          const data = await res.json();
          setDeals(Array.isArray(data) ? data : []);
        } catch (e) {
          setDeals([]);
        }
        setLoading(false);
      });
  }, []);

  if (loading) return <div>Loading deals...</div>;

  return (
    <div style={{ background: '#fafbfc', border: '1px solid #e0e0e0', borderRadius: 0, padding: 0, margin: '0 auto', maxWidth: 900 }}>
      <table style={{ width: '100%', borderCollapse: 'collapse', background: 'none', fontSize: 15 }}>
        <thead>
          <tr style={{ background: '#f4f5f7', borderBottom: '1px solid #e0e0e0' }}>
            <th style={{ fontWeight: 600, color: '#222', fontSize: 15, padding: '12px 8px', border: 'none', textAlign: 'left' }}>Name</th>
            <th style={{ fontWeight: 600, color: '#222', fontSize: 15, padding: '12px 8px', border: 'none', textAlign: 'left' }}>Stage</th>
            <th style={{ fontWeight: 600, color: '#222', fontSize: 15, padding: '12px 8px', border: 'none', textAlign: 'left' }}>Amount</th>
            <th style={{ fontWeight: 600, color: '#222', fontSize: 15, padding: '12px 8px', border: 'none', textAlign: 'left' }}>Owner</th>
            <th style={{ fontWeight: 600, color: '#222', fontSize: 15, padding: '12px 8px', border: 'none', textAlign: 'left' }}>Expected Close</th>
          </tr>
        </thead>
        <tbody>
          {deals.length === 0 && (
            <tr>
              <td colSpan={5} style={{ color: '#bbb', fontSize: 14, textAlign: 'center', padding: '24px 0' }}>───────────────</td>
            </tr>
          )}
          {deals.map((d: any) => (
            <tr key={d.id} style={{ borderBottom: '1px solid #e0e0e0', background: '#fff' }}>
              <td style={{ padding: '10px 8px', border: 'none', color: '#23272f', fontWeight: 500 }}>{d.name}</td>
              <td style={{ padding: '10px 8px', border: 'none', color: '#444' }}>{d.stageId}</td>
              <td style={{ padding: '10px 8px', border: 'none', color: '#444' }}>{d.amount} {d.currency}</td>
              <td style={{ padding: '10px 8px', border: 'none', color: '#888', fontSize: 14 }}>{d.ownerUserId}</td>
              <td style={{ padding: '10px 8px', border: 'none', color: '#888', fontSize: 14 }}>{d.expectedCloseDate ? new Date(d.expectedCloseDate).toLocaleDateString() : ''}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
