"use client";
import DealsTable from '../../../components/tables/DealsTable';
import Link from 'next/link';
import Modal from '../../../components/ui/Modal';
import DealForm from '../../../components/forms/DealForm';
import React, { useState } from 'react';

export default function DealsPage() {
  const [modalOpen, setModalOpen] = useState(false);
  const handleCreate = (data: any) => {
    // TODO: Connect to backend API
    setModalOpen(false);
    // Optionally refresh table data here
  };
  return (
    <div style={{ padding: 24 }}>
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 24 }}>
        <h1>Deals</h1>
        <div style={{ display: 'flex', gap: 12 }}>
          <Link href="/deals/kanban">
            <button style={{ background: '#f4f5f7', color: '#0052cc', border: 'none', borderRadius: 6, padding: '8px 20px', fontWeight: 600, cursor: 'pointer' }}>Kanban View</button>
          </Link>
          <button onClick={() => setModalOpen(true)} style={{ background: '#0052cc', color: '#fff', border: 'none', borderRadius: 6, padding: '8px 20px', fontWeight: 600, cursor: 'pointer' }}>+ New Deal</button>
        </div>
      </div>
      <div style={{ background: '#fff', borderRadius: 8, padding: 24 }}>
        <DealsTable />
      </div>
      <Modal open={modalOpen} onClose={() => setModalOpen(false)} title="New Deal">
        <DealForm onSubmit={handleCreate} />
      </Modal>
    </div>
  );
}
